

@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,700");
@font-face {
    font-family:blake;
    src:url("fonts/Blake.ttf")
   }
   @font-face {
    font-family:steph;
    src:url("fonts/Steph.ttf")
   }

   @font-face {
      font-family:"Source Sans Pro";
      src:url("fonts/SourceSansPro-Regular.ttf")
     }

   .fa, .fas {
      font-size: large;
   }

   .fab {
      font-size: large;
   }

.bg-dark {
	background-color: #151d28 !important;
}
   
h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
	margin-top: 0.7rem;
	margin-bottom: 0rem;
	font-weight: 500;
	line-height: 1.2;
}

   .container-lg, .container-md, .container-sm, .container {
	max-width: 1320px;
}

   .header-tag-container{
      position: relative; 
      min-width: 250px;
   }

   .go-to-middle-vert{
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
   }

   .logo {
      margin-right: 0.9em;
      max-width: 250px;
      margin-left: 0.9em;
      max-height: 75px;
      padding: 0;
      margin-top: 10px;
   }

   body {
       
    font-size: 1.0rem !important;
    background-color: #f1f2f1;
    padding:0;
    margin:0;
    font-family: "Source Sans Pro",sans-serif;
    font-size: 0.9rem;
    line-height:1.5;
    color:#606c71;
    background-color:#f1f2f1;
    color:#413f42
}
   .main-content kbd {
    background-color:#fafbfc;
    border:1px solid #c6cbd1;
    border-bottom-color:#959da5;
    box-shadow:inset 0 -1px 0 #959da5;
    color:#444d56;
    display:inline-block;
    font-size:11px;
    line-height:10px;
    padding:3px 5px;
    vertical-align:middle
   }
   .main-content img {
    max-width:100%
   }
   .main-content h1,
   .main-content h2,
   .main-content h3,
   .main-content h4,
   .main-content h5,
   .main-content h6 {
    margin-top:2rem;
    margin-bottom:1rem;
    font-weight:normal;
    color:#4B81AC
   }
   .main-content p {
    margin-bottom:1em
   }
   .main-content code {
    padding:2px 4px;
    font-family: "Source Sans Pro",sans-serif;
    font-size:0.9rem;
    color:#567482;
    background-color:#f3f6fa;
   }
   .main-content pre {
    padding:0.8rem;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    font-family:"Source Sans Pro",sans-serif;
    font-size: 1rem;
    color:#567482;
    word-wrap:normal;
    background-color:#f3f6fa;
    border:solid 1px #dce6f0;
   }
   .main-content pre>code {
    padding:0;
    margin:0;
    font-size:0.9rem;
    color:#567482;
    word-break:normal;
    white-space:pre;
    background:transparent;
    border:0
   }
   .main-content .highlight {
    margin-bottom:2rem
   }
   .main-content .highlight pre {
    white-space:initial;
    margin-bottom:0;
    word-break:break-word
   }
   .main-content .highlight pre,
   .main-content pre {
    padding:0.8rem;
    overflow:auto;
    font-size:0.9rem;
    line-height:1.45;
    -webkit-overflow-scrolling:touch
   }
   .main-content pre code,
   .main-content pre tt {
    display:inline;
    max-width:initial;
    padding:0;
    margin:0;
    overflow:initial;
    line-height:inherit;
    word-wrap:normal;
    background-color:transparent;
    border:0
   }
   .main-content pre code:before,
   .main-content pre code:after,
   .main-content pre tt:before,
   .main-content pre tt:after {
    content:normal
   }
   .main-content ul,
   .main-content ol {
    margin-top:0
   }
   .main-content blockquote {
    padding:0 1rem;
    margin-left:0;
    color:#819198;
    border-left:0.3rem solid #dce6f0
   }
   .main-content blockquote>:first-child {
    margin-top:0
   }
   .main-content blockquote>:last-child {
    margin-bottom:0
   }
   table {
    background-color: white;
    width:100%;
    max-width:1200px;
    word-break:normal;
    word-break:keep-all;
    -webkit-overflow-scrolling:touch
   }
   table th {    

    color:#fff;
    background:#0266b3
   }
   table th,
   table td {
    vertical-align:top;
    padding:0.5rem 1rem;
    border:1px solid #cfd2d2
   }
   table td>a {
    color:#a6d8ef
   }
   table td>div {
    padding-bottom:5px
   }
   table tr {
    font-size:small
   }
   .main-content dl {
    padding:0
   }
   .main-content dl dt {
    padding:0;
    margin-top:1rem;
    font-size:1rem;
    font-weight:bold
   }
   .main-content dl dd {
    padding:0;
    margin-bottom:1rem
   }
   .main-content hr {
    height:2px;
    padding:0;
    margin:1rem 0;
    background-color:#eff0f1;
    border:0
   }
   .main-content-search {
    font-size:small;
    padding:10px;
    background-color:white;
    margin-bottom: 8px;
   }
   .search-label {
    padding-top:20px;
    padding-bottom:5px
   }
   .search-label label {
    font-weight:bold
   }
   .search-criteria label {
    padding-left:7px
   }
   .search-item {
    margin-top:3px
   }
   .main-content-table {
    background-color:#f1f2f1;
    overflow:auto;
    margin-bottom: 20px;
    padding-right:0px;
    padding-left:calc(var(--bs-gutter-x) / 2);
   }

   .orange {
    color:#f66a0a
   }
   .grey {
    color:#828282
   }
   .svg-icon {
    width:16px;
    height:16px;
    display:inline-block;
    fill:currentColor;
    padding:5px 3px 2px 5px;
    vertical-align:text-bottom
   }
   .topics {
    padding:5px 0px 5px 10px
   }

   .category {
      padding: 3px 10px;
      background: $primary-color;
      color: rgba(255, 255, 255, 0.9);
      border-radius: 20px;
      font-size: 12px;
      line-height: 1.2;
  }
  
  @each $color,
  $value in $tag-colors {
      .text-#{$color} {
          color: $value;
      }
  
      .bg-#{$color} {
          background-color: $value;
      }
  }
